import React from 'react'
import classnames from 'classnames'

import {
    Button,
    Card,
    CardBody,
    FormGroup,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Container,
    Row,
    Col,
} from 'reactstrap'

export default class GuestBook extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            nama:'',
            email:'',
            pesan:'',
            namaFocused:false,
            emailFocused:false,
            pesanFocused:false,
        }
    }
    onChangeInputNama=(e)=>this.setState({nama:e.target.value})
    onChangeInputEmail=(e)=>this.setState({email:e.target.value})
    onChangeInputPesan=(e)=>this.setState({pesan:e.target.value})

    onSubmit=(e)=>{
        e.preventDefault()
        fetch(process.env.SPREADSHEET_GUESTBOOK_URL, {
            method:'POST', 
            body: new FormData(document.forms['form-guest-book'])
            // body: {
            //     nama:this.state.nama,
            //     email:this.state.email,
            //     pesan:this.state.pesan
            // }
        })
        .then(res => alert('Kunjungan tercatat, terima kasih'))
        .catch(err => alert('Proses penyimpanan gagal, terdapat gangguan. Yuk laporkan issue ini di github kami untuk perkembangan website ini. terima kasih'))
    }

    render(){
        return (
            <>
                <section className="section section-lg pt-lg-0 section-contact-us">
                    <Container>
                        <Row className={"justify-content-center " + ((this.props.isTopFloat) ? "mt--300 " : "")}>
                        <Col lg="8">
                            <Card className="bg-gradient-secondary shadow">
                            <CardBody className="p-lg-5">
                            <form name="form-guest-book" onSubmit={this.onSubmit}>
                                <h4 className="mb-1">Buku Tamu Pengunjung</h4>
                                <p className="mt-0">
                                Catat kunjungan anda di buku tamu kami dan kirimkan pesan kepada kami.
                                </p>
                                <FormGroup
                                className={classnames("mt-5", {
                                    focused: this.state.nameFocused
                                })}
                                >
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <i className="ni ni-user-run" />
                                    </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                    placeholder="Nama Lengkap"
                                    type="text"
                                    name="nama"
                                    onFocus={e => this.setState({ nameFocused: true })}
                                    onBlur={e => this.setState({ nameFocused: false })}
                                    onChange={this.onChangeInputNama}
                                    />
                                </InputGroup>
                                </FormGroup>
                                <FormGroup
                                className={classnames({
                                    focused: this.state.emailFocused
                                })}
                                >
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <i className="ni ni-email-83" />
                                    </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                    placeholder="Alamat Email"
                                    type="email"
                                    name="email"
                                    onFocus={e => this.setState({ emailFocused: true })}
                                    onBlur={e => this.setState({ emailFocused: false })}
                                    onChange={this.onChangeInputEmail}
                                    />
                                </InputGroup>
                                </FormGroup>
                                <FormGroup className="mb-4">
                                <Input
                                    className="form-control-alternative"
                                    cols="80"
                                    name="pesan"
                                    placeholder="Tulis sebuah pesan..."
                                    rows="4"
                                    type="textarea"
                                    onChange={this.onChangeInputPesan}
                                />
                                </FormGroup>
                                <div>
                                <Button
                                    block
                                    className="btn-round"
                                    color="default"
                                    size="lg"
                                    type="submit"
                                    
                                >
                                    KIRIM PESAN
                                </Button>
                                </div>
                            </form>
                            </CardBody>
                            </Card>
                        </Col>
                        </Row>
                    </Container>
                    </section>
            </>
        )
    }
}